import React, { useState } from 'react';
import { motion } from "framer-motion";

function MainVisual() {
    const [y, setY] = useState(0);
    const [s, setS] = useState(1);
    const [op, setOp] = useState(1);
    const [ticking, setTicking] = useState(false);     

    function move(scroll_pos) {
        let pos = Math.round((scroll_pos / 10))*10;
        console.log(pos);
        if (pos < 800) {
            if (pos <= 50) {
            setY(0);
            setS(1);
            setOp(1);
            } else {
                setY(pos);
                setOp(0);
                setS(Math.round(10 - pos/100)/10);
            }
        } 
    }

    window.addEventListener('scroll', (e)=> {
        let scrollY = window.pageYOffset;
        if (!ticking) {
          window.requestAnimationFrame(()=> {
            move(scrollY);
            setTicking(false);
          });
          setTicking(true);
        }
      });
    return (
        <section className="intro"> 
            <div className="main_visual" style={{transform: `translateY(${y}px) scale(${s})`, transition: '1s' }}>
                <h2 className="tit" style={{opacity : op, transition: `1s`}}>HELLO, WORLD</h2>
            </div>
        </section>
    )
}


function Intro() {
    return (
        <MainVisual />
    )
}


export default Intro;